/* Main Container Styling */
.bmi {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 50px auto;
    width: fit-content;
    padding: 40px 20px;
    border-radius: 5px;
    background-color: rgb(195, 238, 238);
    box-shadow: 10px 10px 10px rgb(32, 32, 32);
}

/* Input Fields */
.fld {
    padding: 5px 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid gray;
    width: 100%;
    max-width: 300px;
    box-sizing: border-box;
}

/* Button Styling */
.btn {
    padding: 10px 15px;
    margin-top: 5px;
    background-image: linear-gradient(to right, rgb(199, 81, 81), rgb(218, 164, 164));
    border-radius: 5px;
    color: gainsboro;
    border: none;
    cursor: pointer;
    transition: background 0.3s, transform 0.2s;
}

.btn:focus, .btn:hover {
    background-image: linear-gradient(to right, rgb(166, 236, 166), rgb(86, 151, 86));
    color: black;
    transform: scale(1.05);
}

/* Heading Styling */
.h2 {
    font-size: x-large;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 
                 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 0 0 10px;
}

/* Result Section */
.res {
    text-align: center;
    margin-top: 40px;
    font-size: large;
    font-weight: bold;
    font-family: cursive;
    min-height: 100px; /* Ensures consistent height */
    width: 100%;
    max-width: 300px;
    box-sizing: border-box;
    overflow: hidden; /* Prevents overflow issues */
}

/* Responsive Design for Small Screens */
@media (max-width: 768px) {
    .bmi {
        width: 90%;
        padding: 20px;
    }

    .fld {
        max-width: 100%;
    }

    .res {
        min-height: 80px; /* Adjust height for smaller screens */
    }
}
